export default {
	data() {
		return {
			Invite_Code: '',
			Is_Check: false,
			error_User_remind: true,
			User_Error: '',
			WXCode: '',
			UserName: '',

		}

	},
	methods: {

		Submit(e) {

			if (this.UserName.length < 1) {
				uni.showToast({
					title: '请输入用户名',
					icon: 'none',
					mask: false,
					duration: 3000
				});
				return;
			}
			let cuser = this.UserName;
			uni.login({
				provider: 'weixin',
				success: function(res) {
					let currentwxcode = res.code;
					uni.setStorageSync("findapp_wxcode", currentwxcode);
					uni.request({
						url: 'https://zhaoren.wellwinyun.com/index/weChatAuthorization?code=' + res.code + '&name=' + cuser,
						method: 'GET',
						success: result => {
							
							var sucdata = result.data;
							if (sucdata.code == '200') {
								let openid = sucdata.data;
								uni.setStorageSync("findapp_openid", openid);
								uni.redirectTo({
									url: '../backend/searchmain'
								});
							} else if (sucdata.code == '400') {
								uni.showToast({
									title: '用户不存在',
									icon: 'none',
									mask: false,
									duration: 3000
								});
							} else {
								uni.showToast({
									title: '登录失败',
									icon: 'none',
									mask: false,
									duration: 3000
								});
							}
						},
						fail: failres => {
							console.log('登录失败' + failres);
							uni.showToast({
								title: '登录失败',
								icon: 'none',
								mask: false,
								duration: 3000
							});
						},
					});
				},
				fail(fdata) {
					console.log('获取code失败');
					uni.showToast({
						title: '获取code失败',
						icon: 'none',
						mask: false,
						duration: 3000
					});
				}
			})
		}
	},
	onLoad() {
		uni.getStorage({
			key: 'findapp_openid',
			success: function(res) {
				uni.redirectTo({
					url: '../backend/searchmain'
				});
			}
		});
	}
}
